Transforms */
.lt_ulv-error-wrap {
  z-index: 2;
  background-color: #000;
  background-image: url(https://www.lifetime.life/content/dam/mylt/images/live/live-streaming-key.jpg);
}

.lt_ulv-error {
  position: absolute;
  left: 50%;
  top: 50%;
  width: calc(100% - 2rem);
  max-width: 24rem;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.amp-default-skin .vjs-progress-control {
  display: none !important;
}

.amp-default-skin .vjs-time-control {
  visibility: hidden !important;
}

.amp-default-skin .amp-content-title {
  top: auto !important;
  bottom: calc(0.03em) !important;
}

.amp-default-skin.vjs-user-inactive .vjs-playing .amp-content-title {
  display: block !important;
}

